:root {
  --section-overlay: rgba(166, 166, 166, 0.345);
  --background-overlay: rgba(8, 8, 8, 0.2);
}

* {
  margin: 0;;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Raleway', 'Poppins';
}

.App {
  width: 100%;
  height: 100vh;
  background-position: center;
  background-size: cover;
}

.overlay {
  width: 100%;
  height: 100vh;
  background: linear-gradient(#ee2a7c4a,#ff7db746), var(--background-overlay);
}

.container {
  max-width: 800px;
  margin: auto;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 1rem;
}

.section {
  width: 100%;
  padding: 1rem;
  border-radius: 0.4rem;
  color: #1a1a1a;
}

.section__inputs, .section__temperature {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--section-overlay);
}

.section__inputs>input {
  border: none;
  border-bottom: 2px solid #fff;
  border-radius: 0.1rem;
  background-color: transparent;
  color: white;
  width: 50%;
  padding: 0.5rem;
  font-size: 20px;
  font-weight: 200;
}

.section__inputs>input:focus {
  outline: none;
}

.section__inputs>button {
  padding: 10px 40px;
  border: none;
  border-radius: 0.2rem;
  font-size: 20px;
  font-weight: 700;
  background-color: transparent;
  transition: all 300ms ease-in-out;
}

.section__inputs>button:hover {
  cursor: pointer;
  background-color: #fff;
  transition: 0.5s;
}

.icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 24px;
  justify-content: center;
  text-transform: capitalize;
}

icon>h3 {
  font-size: 12px;
  font-weight: 200;
  text-transform: capitalize;
}

.temperature>h1 {
  font-size: 62px;
}